

.container {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(../Images/Detail.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
}

.contDetalle {
    width: 60%;
    /* margin: 2rem; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    border: solid 5px rgba(0, 0, 0, 0.3);
}

.contDetalleIzq {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contDetalleIzq img {
    width: 100%;
    height: auto;
    padding: 1rem;
}

.contDetalleDer {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pokemonInfo {
    width: 100%;
}

.btnHome button {
    top: 50px; /* contando desde el margen de ariiba 50 pixeles desde el borde superior de la pantalla */
    left: 50px;
    width:max-content;
    padding: 1rem;
    cursor: pointer;
    position: absolute;
    border-radius: 10px;
    background-color: rgb(250, 224, 77);

}

.name {

    text-decoration: underline;
    text-shadow: 3px 3px 25px 11px rgb(212, 15, 15);
}

.type {
    width: 50%;
    border-radius: 40px;
    border: solid 5px rgba(0, 0, 0, 0.3);

}