.container {
    height: 100%;
    /* width: 100vw; */
    min-height: 100vh;
    background-image: url(../Images/creacion.webp);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    
}

.titleSubmit{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    
}

.createTitle{
    height: 50%;
    width: 50%;
    display: flex;
    color: rgb(0, 0, 0);
    /* font-weight: lighter; */
    justify-content: center;
    /* text-shadow: 2px 2px 2px rgb(25, 25, 25); */
    margin-top: 10px;
    border: solid 2px rgb(0, 0, 0);
    border-radius: 10px;
    background: rgb(255, 15, 15);
}

.form{
    /* display: flex; */
/*     flex-direction: row;
    justify-content:center;
    align-items: center; */
    
    border: solid 1px rgb(0, 0, 0);
    border-radius: 10px;
    
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.range{
    /* height: 10%; */
    max-height: 50px;
    width: 45%;
    display: flex;
    align-items: center;
    color: rgb(0, 0, 0);
    /* font-weight: lighter; */
    justify-content: space-evenly;
    /* text-shadow: 2px 2px 2px rgb(25, 25, 25); */
    margin-top: 3px;
    border: solid 2px rgb(0, 0, 0);
    border-radius: 10px;
    background: rgb(255, 15, 15);
}

.label{
    text-align: left
}

.type {
    
    /* width: 10%; */
    border-radius: 20px;
    border: solid 2px rgba(0, 0, 0, 0.685);

}

.btn { /* boton */
    padding: 1rem;    
    /* padding: 10px 20px; */
    color: rgb(223, 23, 23);
    border: solid 3px rgb(0, 0, 0);
    border-radius: 100px;
    background: rgb(255, 251, 31);
    
    /* font-family: impact, sans-serif; */
font-size: 18px;
color: #000000;
font-style: normal;
font-weight: normal;
font-variant: normal;
text-align: center;
letter-spacing: 2.1px;
line-height: 18px;

}


.btn:hover {  /* efecto para cuando pasemos por encima del boton */
background-color: #f00000;
border: solid 4px #070424;
color: rgb(254, 255, 246);
box-shadow: 3px 3px 25px 11px rgb(8, 8, 221); /* para darle sombra */
}

.btnHome button {
    top: 20px; /* contando desde el margen de ariiba 50 pixeles desde el borde superior de la pantalla */
    left: 50px;
    width:max-content;
    padding: 1rem;
    cursor: pointer;
    position: absolute;
    border-radius: 10px;
    background-color: rgb(250, 224, 77);

}
